import React, { useRef, useContext } from "react";
import { BrowserRouter as Router, Switch, Route } from "react-router-dom";
import Header from "./component/Header/Header";
import Map from "./component/Map/Map";
import AboutUs from "./component/AboutUs/AboutUs";
import Suggest from "./component/Suggest/Suggest";
import Contact from "./component/Contact/Contact";
import "./App.css";
import Footer from "./component/Footer/Footer";
import HistoryContextProvider from "./context/HistoryContext";
import SuggestModifContextProvider from "./context/SuggestModifContext";
import MapContextProvider from "./context/MapContext";
import PinContextProvider from "./context/PinContext";
import InfoPageContextProvider, {
  InfoPageContext
} from "./context/InfoPageContext";
import AuthContextProvider from "./context/AuthContext";
import ApiContextProvider from "./context/ApiContext";

const AppContent = () => {
  const { legal, about } = useContext(InfoPageContext);

  return (
    <Router>
      <Header />
      <Switch>
        {legal.route}
        {about.route}
        <Route path="/contact" component={Contact} />
        <Route path="/suggest" component={Suggest} />
        <Route path="/about-us" component={AboutUs} />
        <Route path="/" component={Map} />
      </Switch>
      <Footer />
    </Router>
  );
};

function App() {
  const refApp = useRef(null);
  console.log(process.env.REACT_APP_API_URL);
  return (
    <div ref={refApp} className="App">
      {/* <ApiContextProvider> */}
      <InfoPageContextProvider>
        <MapContextProvider>
          <PinContextProvider>
            <AuthContextProvider>
              <AppContent />
            </AuthContextProvider>
          </PinContextProvider>
        </MapContextProvider>
      </InfoPageContextProvider>
      {/* </ApiContextProvider> */}
    </div>
  );
}

export default App;
