import React, { useState, useContext, useRef, useEffect } from "react";
import PropTypes from "prop-types";
import "./Pin.css";
import greenPin from "../../assets/images/green-pin.png";
import orangePin from "../../assets/images/orange-pin.png";
import redPin from "../../assets/images/red-pin.png";
import Card from "../Card/Card";
import { MapContext } from "../../context/MapContext";
import { PinContext } from "../../context/PinContext";

const Pin = ({ showCard, id, cardData }) => {
  const { toggleCard } = useContext(PinContext);
  const phaseToIng = () => {
    switch (cardData.phase) {
      case 1:
        return greenPin;
      case 2:
        return orangePin;
      case 3:
        return redPin;
      default:
        return greenPin;
    }
  };
  const img = phaseToIng(cardData.phase);
  const pinRef = useRef(null);
  const { mapRef } = useContext(MapContext);
  // let pinPos = { x: 0, y: 0 };

  // Set BoundingClientRect, null before first render
  let mapRect = {
    top: null,
    left: null
  };
  let pinRect = {
    top: null,
    left: null
  };

  if (mapRef.current && pinRef.current) {
    mapRect = mapRef.current.getBoundingClientRect();
    pinRect = pinRef.current.getBoundingClientRect();
  }

  // This fx calculate the pos of the pin relative to the map
  const calcPosPin = (pin, map) => {
    const x = pin.left - map.left - 10; // Half width of the pin
    const y = pin.top - map.top;
    return { x, y };
  };

  // This fx calculate the position of the card relative to the pin
  // and sets css classes in cardPos
  const [cardPos, setCardPos] = useState("mid");
  const calcCardPos = pinPos => {
    let position = "";
    if (!pinRect.top || !pinRect.left) return;
    const { width, height } = mapRect;
    if (pinPos.x > width / 2) position += "left";
    else position += "right";
    if (pinPos.y > height / 2) position += " top";
    else position += " bot";
    setCardPos(position);
  };

  useEffect(() => {
    const pinPos = calcPosPin(pinRect, mapRect);
    calcCardPos(pinPos);
    // setCardPos(calcCardPos(pinPos));
  });

  return (
    <div ref={pinRef} className="pin">
      <button type="button" onClick={() => toggleCard(id)}>
        <img src={img} alt="pin" />
      </button>
      {showCard && <Card cardPos={cardPos} cardData={cardData} />}
    </div>
  );
};

Pin.defaultProps = {};

Pin.propTypes = {
  showCard: PropTypes.bool.isRequired,
  id: PropTypes.number.isRequired,
  cardData: PropTypes.shape({
    title: PropTypes.string,
    phase: PropTypes.number,
    description: PropTypes.string,
    address: PropTypes.string,
    updatedAt: PropTypes.string
  }).isRequired
};

export default Pin;
