import { useReducer } from "react";
import PropTypes from "prop-types";
import { apiFetching, apiSuccess, apiError } from "../action/actionCreators";
import ApiReducer, { initialState } from "../reducer/ApiReducer";

export const initialParams = {
  body: null,
  headers: null
};

export const jsonHeader = {
  "Content-Type": "application/json"
};

const useApi = endpoint => {
  const [response, dispatch] = useReducer(ApiReducer, initialState);

  // const fetchApi = useCallback(
  //   async (route, verb, { body, headers } = initialParams) => {
  //     const url = endpoint + route;
  //     const settings =
  //       verb.toUpperCase() === "POST"
  //         ? {
  //             method: verb,
  //             body,
  //             headers
  //           }
  //         : {
  //             method: verb
  //           };
  //     console.log(settings);
  //     dispatch(apiFetching());
  //     await fetch(url, { ...settings })
  //       .then(res => res.json())
  //       .then(res => {
  //         dispatch(apiSuccess({ res, url, ...settings }));
  //         console.log("get success");
  //       })
  //       .catch(err => {
  //         dispatch(apiError({ err, url, ...settings }));
  //       });
  //   }
  // );

  const fetchApi = (route, verb, { body, headers } = initialParams) => {
    const url = endpoint + route;
    const settings =
      verb.toUpperCase() === "POST"
        ? {
            method: verb,
            body,
            headers
          }
        : {
            method: verb
          };
    dispatch(apiFetching());
    return fetch(url, { ...settings })
      .then(res => res.json())
      .then(res => {
        dispatch(apiSuccess({ res, url, ...settings }));
        return res;
      })
      .catch(err => {
        console.log(err);
        dispatch(apiError({ err, url, ...settings }));
      });
  };

  const get = route => {
    return fetchApi(route, "get");
  };

  const post = (route, params) => {
    return fetchApi(route, "post", params);
  };

  return [response, { get, post }];
};

useApi.propTypes = {
  uri: PropTypes.string.isRequired
};

export default useApi;
