import { FETCHING, SUCCESS, ERROR, UNSET } from "../actionType/actionTypes";

export const initialeState = {
  status: null,
  response: null
};

const ApiRequestReducer = (state = initialeState, { type, response } = {}) => {
  switch (type) {
    case FETCHING:
      return { ...initialeState, status: FETCHING };
    case SUCCESS:
      return { ...state, status: SUCCESS, response };
    case ERROR:
      return { ...state, status: ERROR, response };
    case UNSET:
      return { ...state, status: UNSET };
    default:
      return { ...state, status: UNSET };
  }
};

export default ApiRequestReducer;
