import React, { useState, useContext, useEffect } from "react";
import GoogleMapReact from "google-map-react";
import SearchBar from "../SearchBar/SearchBar";
import "./Map.css";
import SuggestModif from "../SuggestModif/SuggestModif";
import History from "../History/History";
import Pin from "../Pin/Pin";
import { MapContext } from "../../context/MapContext";
import { PinContext } from "../../context/PinContext";

const Map = () => {
  const key = "AIzaSyAr2ugrwLtCWxdkM1qLJbgbCPzQqr9oC14";
  const [center, setCenter] = useState({
    lat: 43.5619687,
    lng: 1.467865
  });
  const [zoom] = useState(11);
  const { history } = useContext(PinContext);
  const { mapRef } = useContext(MapContext);
  const { pins, suggest } = useContext(PinContext);

  const Pins =
    pins &&
    Object.keys(pins)
      .map(k => pins[k])
      .map(pin => {
        const cardData = {
          id: pin.id,
          title: pin.title,
          phase: pin.phase,
          description: pin.description,
          address: pin.address,
          updatedAt: pin.updated_at,
          userConf: pin.userConf,
          userSuggest: pin.userSuggest,
          userinfo: pin.userinfo
        };
        // theme not yet defined
        return (
          <Pin
            key={pin.id}
            id={pin.id}
            lat={pin.lat}
            lng={pin.lng}
            showCard={pin.showCard}
            cardData={cardData}
          />
        );
      });

  return (
    <>
      <div ref={mapRef} className="map" role="button" tabIndex={0}>
        {suggest.show && <SuggestModif />}
        {history && <History />}
        <SearchBar mapRef={mapRef} center={center} setCenter={setCenter} />
        <div className="map-wrapper">
          <GoogleMapReact
            bootstrapURLKeys={{ key }}
            defaultCenter={center}
            center={center}
            defaultZoom={zoom}
          >
            {Pins}
          </GoogleMapReact>
        </div>
      </div>
    </>
  );
};

export default Map;
