import React, { useContext } from "react";
import facebookLogo from "../../assets/images/fb-icon.png";
import googleLogo from "../../assets/images/google-icon.png";
import instagramLogo from "../../assets/images/insta-icon.png";
import { AuthContext } from "../../context/AuthContext";

import "./LoginButtons.css";

const LoginButtons = () => {
  const { login } = useContext(AuthContext);
  const url =
    "https://api.instagram.com/oauth/authorize?client_id=462870114620083&redirect_uri=https://localhost:3000&scope=user_profile,user_media&response_type=code";

  const redirectTo = redirectUrl => {
    window.location = redirectUrl;
  };

  const instagramLogin = () => {
    fetch(url)
      .then(res => {
        console.log("instagram success");
        console.log(res);
        if (res.status === 200) {
          redirectTo(res.url);
        }
      })
      .catch(err => {
        console.log("instagram error");
        console.log(err);
      });
  };

  return (
    <div className="login-btns">
      <p>Identifiez-vous pour intragir</p>
      <div className="wrapper">
        <button type="button" onClick={() => login("facebook")}>
          <img src={facebookLogo} alt="Facebook" />
        </button>
        <button type="button" onClick={() => login("google")}>
          <img src={googleLogo} alt="Google" />
        </button>
        <button type="button" onClick={() => instagramLogin()}>
          <img src={instagramLogo} alt="Instagram" />
        </button>
      </div>
    </div>
  );
};

export default LoginButtons;
