import { SHOW_MENU, HIDE_MENU, TOGGLE_MENU } from "../actionType/actionTypes";

const MenuReducer = (state, action) => {
  switch (action.type) {
    case SHOW_MENU:
      return true;
    case HIDE_MENU:
      return false;
    case TOGGLE_MENU:
      return !state;
    default:
      return state;
  }
};

export default MenuReducer;
