import React from "react";
import PropTypes from "prop-types";
import uuid from "uuid/v1";
import "./Upload.css";

const Upload = ({ domId, files, setFiles }) => {
  const uploadFiles = event => {
    if (event.target.files) setFiles(event.target.files);
  };

  // const listFiles =
  //   files &&
  //   files.map(file => (
  //     <div key={uuid()} className="file">
  //       <p>{file.name}</p>
  //       <button type="button">x</button>
  //     </div>
  //   ));

  return (
    <div className="upload">
      <p>Pice(s) jointe</p>
      <label htmlFor={domId}>
        choisir
        <input
          id={domId}
          type="file"
          multiple="multiple"
          onChange={uploadFiles}
          hidden
        />
      </label>
      {/* <div className="content">{listFiles}</div> */}
    </div>
  );
};

Upload.defaultsProps = {
  files: null
};

Upload.propTypes = {
  domId: PropTypes.string.isRequired,
  setFiles: PropTypes.func.isRequired
};

export default Upload;
