import React, { createContext, useReducer } from "react";
import PropTypes from "prop-types";
import SuggestModifReducer, {
  initialState
} from "../reducer/SuggestModifReducer";

export const SuggestModifContext = createContext();

const SuggestModifContextProvider = ({ children }) => {
  const [suggestModif, dispatch] = useReducer(
    SuggestModifReducer,
    initialState
  );

  return (
    <SuggestModifContext.Provider value={{ suggestModif, dispatch }}>
      {children}
    </SuggestModifContext.Provider>
  );
};

SuggestModifContextProvider.propTypes = {
  children: PropTypes.node.isRequired
};

export default SuggestModifContextProvider;
