import { useReducer, useCallback } from "react";
import ApiRequestReducer, { initialeState } from "../reducer/ApiRequestReducer";
import { fetching, success, error, unset } from "../action/actionCreators";

// function sleep(ms) {
//   return new Promise(resolve => setTimeout(resolve, ms));
// }

const useApiRequest = (endpoint, { verb = "get", params = {} } = {}) => {
  const [state, dispatch] = useReducer(ApiRequestReducer, initialeState);

  const makeRequest = useCallback(async () => {
    dispatch(fetching());
    const settings = verb.toUpperCase() === "POST" && {
      body: JSON.stringify(params),
      headers: {
        "Content-Type": "application/json"
      }
    };
    await fetch(endpoint, { ...settings, method: verb })
      .then(res => res.json())
      .then(res => {
        console.log(res);
        dispatch(success(res));
        dispatch(unset());
      })
      .then(res => {
        console.log(res);
      })
      .catch(async err => {
        console.log(`err ${err}`);
        dispatch(error(err));
      });
  }, [endpoint, verb, params]);

  const resetStatus = () => {
    dispatch(unset());
  };

  return [state, { makeRequest, resetStatus }];
};

export default useApiRequest;
