import React, { useState, useContext } from "react";
import "./History.css";
import exitCross from "../../assets/images/black-x.png";
import arrow from "../../assets/images/arrow.png";
import { PinContext } from "../../context/PinContext";

const History = () => {
  const [showConfirm, setConfirm] = useState(false);
  const [showOld, setOld] = useState(false);
  const { history, closeHistory } = useContext(PinContext);
  const {
    title,
    phase,
    description,
    address,
    updatedAt,
    userConf,
    userSuggest,
    userinfo
  } = history;

  const countValidConfirmation = () => {
    return userConf.filter(
      conf => conf && conf.id && conf.createdAt && conf.userinfo
    ).length;
  };

  const phaseToStyle = () => {
    switch (phase) {
      case 1:
        return "green";
      case 2:
        return "orange";
      case 3:
        return "red";
      default:
        return "green";
    }
  };

  return (
    <div className="history">
      <div className="relative">
        <div className="sticky">
          <div className="container">
            <button
              type="button"
              className="exit"
              onClick={() => closeHistory()}
            >
              <img src={exitCross} alt="exit" />
            </button>
            <h1>{title}</h1>
            <div className={`phase ${phaseToStyle()}`}>
              <p>{phase}</p>
            </div>
            <div className="last">
              <p className="author">{userinfo && userinfo.username}</p>
              <p className="description">{description}</p>
              <p className="date">{updatedAt}</p>
            </div>
            <button
              className="confirm"
              type="button"
              onClick={() => setConfirm(!showConfirm)}
            >
              <img
                className={showConfirm ? "rotate" : ""}
                src={arrow}
                alt="arrow"
              />
              {`${countValidConfirmation()} personne(s) ont confirm cet attribut`}
            </button>
            <div
              className={`confirm-drop-down ${showConfirm ? "show" : "hide"}`}
            >
              <ul>
                {userConf.map(({ userinfo: user, createdAt }) => {
                  return (
                    user &&
                    user.id && (
                      <li key={user.id}>
                        {user && user.username}  confirm(e) le
                        {createdAt}
                      </li>
                    )
                  );
                })}
              </ul>
            </div>
            <button
              className="old"
              type="button"
              onClick={() => setOld(!showOld)}
            >
              <img
                className={showOld ? "rotate" : ""}
                src={arrow}
                alt="arrow"
              />
              Liste des anciennes attribution
            </button>
            <div className={`old-drop-down  ${showOld ? "show" : "hide"}`}>
              <ul>
                {userSuggest.map(
                  ({ description: descrip, createdAt, userinfo: user }) => {
                    return (
                      user &&
                      user.id && (
                        <li key={user.id}>
                          <p className="author">
                            Le {createdAt} par {user.username}
                          </p>
                          <p className="description">{descrip}</p>
                        </li>
                      )
                    );
                  }
                )}
              </ul>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default History;
