import {
  SHOW_CARD,
  CLOSE_CARDS,
  CLOSE_CARD,
  SET_PINS,
  TOGGLE_CARD,
  CONFIRM_CARD
} from "../actionType/actionTypes";

const PinReducer = (state = {}, { type, payload }) => {
  switch (type) {
    case SHOW_CARD:
      return Object.values(state).map(pin => {
        if (pin.id === payload.projectId) return { ...pin, showCard: true };
        return pin;
      });
    case TOGGLE_CARD:
      return Object.values(state).map(pin => {
        if (pin.id === payload.projectId)
          return { ...pin, showCard: !pin.showCard };
        return { ...pin, showCard: false };
      });
    case CLOSE_CARD:
      return Object.values(state).map(pin => {
        if (pin.id === payload.projectId) return { ...pin, showCard: false };
        return pin;
      });
    case CLOSE_CARDS:
      return Object.values(state).map(pin => {
        return { ...pin, showCard: false };
      });
    case SET_PINS:
      return { ...state, ...payload.projects };
    case CONFIRM_CARD:
      return Object.values(state).map(pin => {
        if (pin.id === payload.projectId) {
          return { ...pin, userConf: payload.userConf };
        }
        return { ...pin };
      });
    default:
      return { ...state };
  }
};

export default PinReducer;
