import { useReducer, useEffect } from "react";
import {
  showCard as showCardAction,
  closeCards as closeCardsAction,
  closeCard as closeCardAction,
  setPins,
  confirmCard as confirmCardAction,
  toggleCard as toggleCardAction
} from "../action/actionCreators";
import PinReducer from "../reducer/PinReducer";
import { API_URL } from "../config/global";

const usePin = () => {
  const [pins, dispatch] = useReducer(PinReducer, null);

  const closeCards = () => {
    dispatch(closeCardsAction());
  };

  const closeCard = projectId => {
    dispatch(closeCardAction(projectId));
  };

  const toggleCard = projectId => {
    dispatch(toggleCardAction(projectId));
  };

  const showCard = projectId => {
    dispatch(showCardAction(projectId));
  };

  const confirmCard = (projectId, userId, oldUserConf) => {
    const OldUserConf = oldUserConf ? { ...oldUserConf } : {};
    fetch(`${API_URL}/projects/${projectId}`, {
      method: "PUT",
      headers: {
        "Content-Type": "application/json"
      },
      body: JSON.stringify({
        userConf: [
          {
            createdAt: new Date(),
            userinfo: {
              id: userId
            }
          },
          OldUserConf
        ]
      })
    })
      .then(res => res.json())
      .then(res => {
        console.log(res);
        dispatch(confirmCardAction(projectId, res.userConf));
      })
      .catch(err => {
        console.log(err);
      });
  };

  const initialize = () => {
    fetch(`${API_URL}/projects`)
      .then(res => res.json())
      .then(res => {
        // Adding showCard bool to each pin
        const Pins = Object.values(res).map(pin => {
          return { ...pin, showCard: false };
        });
        dispatch(setPins(Pins));
      });
  };

  useEffect(() => {
    initialize();
  }, []);

  return [pins, { showCard, toggleCard, closeCard, closeCards, confirmCard }];
};

export default usePin;
