import React, { useState } from "react";
import Input from "../utils/UserInput/Input/Input";
import TextArea from "../utils/UserInput/TextArea/TextArea";
import Button from "../utils/Button/Button";
import "./Contact.css";
import Upload from "../utils/Upload/Upload";
import FadeIn from "../utils/FadeIn/FadeIn";
// import Spinner from "../utils/Spinner/Spinner";
import Modal from "../utils/Modal/Modal";
import { API_URL } from "../../config/global";

const Contact = () => {
  const [name, setName] = useState("");
  const [email, setEmail] = useState("");
  const [message, setMessage] = useState("");
  const [files, setFiles] = useState(null);
  const [errMsg, setErrMsg] = useState();
  // For CSSTransition > FadeIn and Modal
  const [modalSuccess, setModalSuccess] = useState(false);
  const [modalError, setModalError] = useState(false);
  // Fetch Files
  const fetchFiles = refId => {
    if (!files) return;
    const formData = new FormData();
    Object.values(files).forEach(file => {
      formData.append("files", file);
    });
    formData.append("ref", "message");
    formData.append("refId", refId);
    formData.append("field", "file");

    fetch(`${API_URL}/upload`, {
      method: "POST",
      body: formData
    })
      .then(res => res.json())
      .then(res => {
        console.log(res);
      })
      .catch(err => {
        console.log(err);
      });
  };
  const fetchData = () => {
    fetch(`${API_URL}/messages`, {
      method: "POST",
      body: JSON.stringify({
        name,
        email,
        message
      })
    })
      .then(res => res.json())
      .then(res => {
        setModalSuccess(true);
        setTimeout(() => {
          setModalSuccess(false);
        }, 4000);
        fetchFiles(res.id);
      })
      .catch(error => {
        setErrMsg(error);
        console.log(error);
        setModalError(true);
        setTimeout(() => {
          setModalError(false);
        }, 4000);
      });
  };
  const handleSubmit = e => {
    e.preventDefault();
    fetchData();
    setName("");
    setEmail("");
    setMessage("");
    setFiles(null);
  };

  return (
    <div className="contact">
      {/* {status === FETCHING && (
        <div className="fetching">
          <Spinner />
        </div>
      )} */}
      <FadeIn inProp={modalSuccess}>
        <div className="modal-container">
          <Modal status="success" closeModal={() => setModalSuccess(false)}>
            Votre message a bien t envoyer !
          </Modal>
        </div>
      </FadeIn>
      <FadeIn inProp={modalError}>
        <div className="modal-container">
          <Modal status="error" closeModal={() => setModalError(false)}>
            Une erreur est survenue {JSON.stringify(errMsg)}
          </Modal>
        </div>
      </FadeIn>
      <form onSubmit={handleSubmit}>
        <Input
          domId="contact-name"
          label="Nom"
          value={name}
          setValue={setName}
        />
        <Input
          domId="contact-email"
          label="Email"
          value={email}
          setValue={setEmail}
        />
        <TextArea
          domId="contact-message"
          label="Message"
          value={message}
          setValue={setMessage}
        />
        <Upload domId="upload" setFiles={setFiles} />
        <Button type="submit">Confirmer</Button>
      </form>
    </div>
  );
};

export default Contact;
