import {
  API_FETCHING,
  API_SUCCESS,
  API_ERROR,
  API_UNKNOWN
} from "../actionType/actionTypes";

export const initialState = {
  status: API_UNKNOWN,
  response: null
};

const ApiReducer = (state = initialState, { type, payload } = {}) => {
  switch (type) {
    case API_FETCHING:
      return { ...initialState, status: API_FETCHING };
    case API_SUCCESS:
      return { ...state, status: API_SUCCESS, response: payload.response };
    case API_ERROR:
      return { ...state, status: API_ERROR, response: payload.response };
    case API_UNKNOWN:
      return { ...state, status: API_UNKNOWN };
    default:
      return { ...state };
  }
};

export default ApiReducer;
