import { useReducer } from "react";
import {
  openHistory as openHistoryAction,
  closeHistory as closeHistoryAction
} from "../action/actionCreators";
import HistoryReducer, { initialState } from "../reducer/HistoryReducer";

const useHistory = () => {
  const [state, dispatch] = useReducer(HistoryReducer, initialState);

  const openHistory = cardData => {
    console.log(cardData);
    dispatch(openHistoryAction(cardData));
  };

  const closeHistory = () => {
    dispatch(closeHistoryAction());
  };

  return [state, { openHistory, closeHistory }];
};

export default useHistory;
