import React, { createContext, useEffect } from "react";
import PropTypes from "prop-types";
import usePin from "../hook/usePin";
import useSuggestModif from "../hook/useSuggestModif";
import useHistory from "../hook/useHistory";

// The PinContext mainly allows to control open/close the cards from the pin
export const PinContext = createContext();

const PinContextProvider = ({ children }) => {
  const [
    pins,
    { showCard, toggleCard, closeCard, closeCards, confirmCard }
  ] = usePin();
  const [
    { state: suggest, currentProjectSuggest },
    { openSuggest, closeSuggest, postSuggest }
  ] = useSuggestModif();
  const [history, { openHistory, closeHistory }] = useHistory();

  useEffect(() => {
    console.log(history);
  }, [history]);

  return (
    <PinContext.Provider
      value={{
        pins,
        showCard,
        toggleCard,
        closeCard,
        closeCards,
        confirmCard,
        suggest,
        openSuggest,
        closeSuggest,
        postSuggest,
        currentProjectSuggest,
        history,
        openHistory,
        closeHistory
      }}
    >
      {children}
    </PinContext.Provider>
  );
};

PinContextProvider.propTypes = {
  children: PropTypes.node.isRequired
};

export default PinContextProvider;
