import React from "react";
import Nav from "../Nav/Nav";
import "./Header.css";

const Header = () => {
  const title = "Vous faire profiter pleinement de vos projets";
  return (
    <header className="header">
      <Nav />
      <h1 className="title">{title}</h1>
      <div className="banner" />
    </header>
  );
};

export default Header;
