import React from "react";
import PropTypes from "prop-types";
import exit from "../../../assets/images/crosscircle-icon.png";
import "./Modal.css";

const Modal = ({ children, status, closeModal }) => {
  return (
    <div
      className={`modal ${
        status.toUpperCase() === "ERROR" ? "error" : "success"
      }`}
    >
      <p>{children}</p>
      <button type="button" onClick={closeModal} onKeyDown={closeModal}>
        <img src={exit} alt="exit cross" />
      </button>
    </div>
  );
};

Modal.defaultProps = {
  status: "success"
};

Modal.propTypes = {
  children: PropTypes.node.isRequired,
  status: PropTypes.string,
  closeModal: PropTypes.func.isRequired
};

export default Modal;
