import React, { useContext } from "react";
import PropTypes from "prop-types";
import "./Card.css";
import Button from "../utils/Button/Button";
import exitCross from "../../assets/images/crosscircle-icon.png";
import { PinContext } from "../../context/PinContext";
import LoginButtons from "../LoginButtons/LoginButtons";
import { AuthContext } from "../../context/AuthContext";
import { FACEBOOK_CONNECTED } from "../../actionType/actionTypes";

const Card = ({ cardPos, cardData, style }) => {
  const { closeCard, confirmCard, openSuggest, openHistory } = useContext(
    PinContext
  );
  const { userinfo } = useContext(AuthContext);
  const { title, phase, description } = cardData;
  const updatedAt = new Date(cardData.updatedAt).toLocaleDateString();

  const isConfirmed = () => {
    let confirm = false;
    if (!cardData.userConf) return confirm;
    cardData.userConf.forEach(userConf => {
      if (userConf.userinfo && userConf.userinfo.id === userinfo.id)
        confirm = true;
    });
    return confirm;
  };

  const handleConfirm = () => {
    confirmCard(cardData.id, userinfo.id, cardData.userConf);
  };

  const phaseToStyle = () => {
    switch (cardData.phase) {
      case 1:
        return "green";
      case 2:
        return "orange";
      case 3:
        return "red";
      default:
        return "green";
    }
  };

  return (
    <div className={`card ${cardPos}`} style={style}>
      <div className={`card-header ${phaseToStyle()}`}>
        <h1>{phase}</h1>
        <div className="card-exit">
          <button type="button" onClick={() => closeCard(cardData.id)}>
            <img src={exitCross} alt="exit icon" />
          </button>
        </div>
      </div>
      <div className="card-body">
        <h1 className="title">{title}</h1>
        <p className="description">{description}</p>
        <p className="date">{`Mise  jour le ${updatedAt}`}</p>
      </div>
      <div className="card-connect">
        {userinfo.status !== FACEBOOK_CONNECTED && <LoginButtons />}
      </div>
      <div className="card-action">
        {!isConfirmed() && (
          <div className="confirm-btn">
            <Button type="button" onClick={() => handleConfirm()}>
              Confirmer
            </Button>
          </div>
        )}
        <div className="suggest-btn">
          <Button type="button" onClick={() => openSuggest(cardData)}>
            Suggrer une modification
          </Button>
        </div>
      </div>
      <div className="card-history">
        <button type="button" onClick={() => openHistory(cardData)}>
          Voir l&apos;historique des suggestions
        </button>
      </div>
    </div>
  );
};

Card.propTypes = {
  cardPos: PropTypes.string.isRequired,
  cardData: PropTypes.shape({
    id: PropTypes.number,
    title: PropTypes.string,
    phase: PropTypes.number,
    description: PropTypes.string,
    address: PropTypes.string,
    updatedAt: PropTypes.string,
    userConf: PropTypes.arrayOf(
      PropTypes.shape({
        id: PropTypes.number,
        dateOfConf: PropTypes.string,
        userinfo: PropTypes.shape({
          id: PropTypes.number,
          username: PropTypes.string,
          userId: PropTypes.string
        })
      })
    )
  }).isRequired
};

export default Card;
