import React, { useState, useContext } from "react";
import "./SuggestModif.css";
import SelectPhase from "../utils/SelectPhase/SelectPhase";
import TextArea from "../utils/UserInput/TextArea/TextArea";
import Button from "../utils/Button/Button";
import exitCross from "../../assets/images/black-x.png";
import { PinContext } from "../../context/PinContext";
import { AuthContext } from "../../context/AuthContext";

const SuggestModif = () => {
  const [description, setDescription] = useState();
  const [justify, setJustify] = useState();
  const [phase, setPhase] = useState();
  const { closeSuggest, postSuggest } = useContext(PinContext);
  const { userinfo } = useContext(AuthContext);

  return (
    <div className="suggest-modif">
      <div className="relative">
        <div className="sticky">
          <div className="container">
            <button
              type="button"
              className="exit"
              onClick={() => closeSuggest()}
            >
              <img src={exitCross} alt="exit" />
            </button>
            <h1>Suggrer une modification</h1>
            <SelectPhase
              label="Modifiez la phase si besoin"
              setPhase={setPhase}
            />
            <TextArea
              domId="description-suggestion"
              label="Description"
              value={description}
              setValue={setDescription}
            />
            <TextArea
              domId="justify-suggestion"
              label="Justifiez brievement votre choix de phase"
              value={justify}
              setValue={setJustify}
            />
            <Button
              type="button"
              onClick={() =>
                postSuggest(userinfo.id, {
                  phase,
                  description,
                  justify
                })
              }
            >
              Soumettre
            </Button>
          </div>
        </div>
      </div>
    </div>
  );
};

export default SuggestModif;
