import {
  FETCHING,
  SUCCESS,
  ERROR,
  UNSET,
  OPEN_HISTORY,
  CLOSE_HISTORY,
  OPEN_SUGGEST,
  CLOSE_SUGGEST,
  POST_SUGGEST,
  CALCULATE_PIN_POSITION,
  SHOW_CARD,
  CLOSE_CARDS,
  CLOSE_CARD,
  SET_PINS,
  CONFIRM_CARD,
  TOGGLE_CARD,
  FACEBOOK_CONNECTED,
  FACEBOOK_NOT_AUTHORIZED,
  FACEBOOK_UNKNOWN,
  GOOGLE_CONNECTED,
  GOOGLE_NOT_AUTHORIZED,
  GOOGLE_UNKNOWN,
  API_FETCHING,
  API_SUCCESS,
  API_ERROR,
  API_UNKNOWN
} from "../actionType/actionTypes";

// useApiRequest
export const fetching = () => ({ type: FETCHING });
export const success = response => ({ type: SUCCESS, response });
export const error = response => ({ type: ERROR, response });
export const unset = () => ({ type: UNSET });

// HistoryContext
export const openHistory = history => ({
  type: OPEN_HISTORY,
  payload: {
    history
  }
});
export const closeHistory = () => ({ type: CLOSE_HISTORY });

// SuggestModifContext
export const openSuggest = projectId => ({
  type: OPEN_SUGGEST,
  payload: {
    projectId
  }
});
export const closeSuggest = () => ({ type: CLOSE_SUGGEST });
export const postSuggest = (projectId, userSuggest) => ({
  type: POST_SUGGEST,
  payload: {
    projectId,
    userSuggest
  }
});

// Map context
export const calculatePinPosition = () => ({ type: CALCULATE_PIN_POSITION });

// Pin context
export const showCard = projectId => ({
  type: SHOW_CARD,
  payload: {
    projectId
  }
});
export const closeCards = () => ({ type: CLOSE_CARDS });
export const closeCard = projectId => ({
  type: CLOSE_CARD,
  payload: {
    projectId
  }
});
export const setPins = projects => ({
  type: SET_PINS,
  payload: {
    projects
  }
});
export const confirmCard = (projectId, userConf) => ({
  type: CONFIRM_CARD,
  payload: {
    projectId,
    userConf
  }
});

export const toggleCard = projectId => ({
  type: TOGGLE_CARD,
  payload: {
    projectId
  }
});

// Auth context

export const facebookConnected = (username, userId, id) => ({
  type: FACEBOOK_CONNECTED,
  payload: {
    username,
    userId,
    id
  }
});
export const facebookNotAuthorized = () => ({ type: FACEBOOK_NOT_AUTHORIZED });
export const facebookUnknown = () => ({ type: FACEBOOK_UNKNOWN });
export const googleConnected = (username, userId, id) => ({
  type: GOOGLE_CONNECTED,
  payload: {
    username,
    userId,
    id
  }
});
export const googleNotAuthorized = () => ({ type: GOOGLE_NOT_AUTHORIZED });
export const googleUnknown = () => ({ type: GOOGLE_UNKNOWN });

// Api context
export const apiFetching = () => ({ type: API_FETCHING });
export const apiSuccess = response => ({
  type: API_SUCCESS,
  payload: response
});
export const apiError = response => ({
  type: API_ERROR,
  payload: response
});
export const apiUnknown = () => ({ type: API_UNKNOWN });
