import React, { useState } from "react";
import PropTypes from "prop-types";
import { animateScroll as scroll } from "react-scroll";
import PlacesAutocomplete, {
  geocodeByAddress,
  getLatLng
} from "react-places-autocomplete";
import searchIcon from "../../assets/images/search-icon.png";
import "./SearchBar.css";

const SearchBar = ({ mapRef, center, setCenter }) => {
  const placeholder = "Entrez une adresse";
  const [address, setAddress] = useState("");

  const handleSearch = event => {
    event.preventDefault();
  };

  // Placeautocomplete
  const handleChange = addr => {
    setAddress(addr);
  };
  const handleSelect = addr => {
    setAddress(addr);
    geocodeByAddress(addr)
      .then(results => getLatLng(results[0]))
      .then(latLng => {
        // addPinFromAddress({ addr, lat: latLng.lat, lng: latLng.lng });
        setCenter({ lat: latLng.lat, lng: latLng.lng });
      })
      .catch(error => console.error("Error", error));
  };

  return (
    <div className="search-bar">
      <form className="search-form" onSubmit={handleSearch}>
        <div className="wrapper">
          <img className="search-icon" src={searchIcon} alt="Search Icon" />
        </div>
        {/* <input
          className="search-input"
          type="text"
          placeholder={placeholder}
          value={address}
          onChange={e => setAddress(e.target.value)}
          onFocus={() =>
            scroll.scrollTo(mapRef.current.offsetTop - 10, { duration: 500 })
          }
        /> */}
        <PlacesAutocomplete
          value={address}
          onChange={handleChange}
          onSelect={handleSelect}
        >
          {({ getInputProps, suggestions, getSuggestionItemProps }) => {
            const inputProps = getInputProps({
              className: "location-search-input"
            });
            return (
              <div className="input-address">
                <input
                  required
                  label="Adresse"
                  type={inputProps.type}
                  autoComplete={inputProps.autoComplete}
                  role={inputProps.role}
                  aria-autocomplete={inputProps["aria-autocomplete"]}
                  aria-expanded={inputProps["aria-expanded"]}
                  placeholder={placeholder}
                  className={`${inputProps.className} search-input`}
                  value={inputProps.value}
                  disabled={inputProps.disable}
                  onKeyDown={inputProps.onKeyDown}
                  onBlur={() => {
                    inputProps.onBlur();
                    if (suggestions[0] && suggestions[0].description)
                      handleSelect(suggestions[0].description);
                  }}
                  onChange={inputProps.onChange}
                  onFocus={() =>
                    scroll.scrollTo(mapRef.current.offsetTop - 10, {
                      duration: 500
                    })
                  }
                />
                <div
                  className={`dropdown ${
                    suggestions.length > 0 ? "show" : "hide"
                  }`}
                >
                  <div className="autocomplete-dropdown-container">
                    {suggestions.map(suggestion => {
                      const className = suggestion.active
                        ? "suggestion-item--active"
                        : "suggestion-item";
                      // inline style for demonstration purpose
                      const style = suggestion.active
                        ? { backgroundColor: "#fafafa", cursor: "pointer" }
                        : { backgroundColor: "#ffffff", cursor: "pointer" };
                      const suggestionItemProps = getSuggestionItemProps(
                        suggestion,
                        {
                          className,
                          style
                        }
                      );
                      return (
                        <div
                          key={suggestionItemProps.key}
                          id={suggestionItemProps.id}
                          role="option"
                          aria-selected={false}
                          tabIndex={0}
                          onMouseEnter={suggestionItemProps.onMouseEnter}
                          onMouseLeave={suggestionItemProps.onMouseLeave}
                          onMouseDown={suggestionItemProps.onMouseDown}
                          onMouseUp={suggestionItemProps.onMouseUp}
                          onTouchStart={suggestionItemProps.onTouchStart}
                          onTouchEnd={suggestionItemProps.onTouchEnd}
                          onClick={suggestionItemProps.onClick}
                          onKeyDown={suggestionItemProps.onClick}
                          className={suggestionItemProps.className}
                          style={suggestionItemProps.style}
                        >
                          <span>{suggestion.description}</span>
                        </div>
                      );
                    })}
                  </div>
                </div>
              </div>
            );
          }}
        </PlacesAutocomplete>
      </form>
    </div>
  );
};

SearchBar.defaultProps = {
  mapRef: null,
  center: null
};

SearchBar.propTypes = {
  mapRef: PropTypes.oneOfType([
    PropTypes.func,
    PropTypes.shape({ current: PropTypes.any })
  ]),
  center: PropTypes.shape({ x: PropTypes.number, y: PropTypes.number }),
  setCenter: PropTypes.func.isRequired
};

export default SearchBar;
