import React from "react";
import Proptypes from "prop-types";
import SelectPhaseInput from "../SelectPhaseInput/SelectPhaseInput";
import "./SelectPhase.css";

const GREEN_THEME = "green";
const ORANGE_THEME = "orange";
const RED_THEME = "red";

const SelectPhase = ({ label, setPhase }) => {
  const name = "phase";
  return (
    <div className="select-phase">
      <p>{label}</p>
      <SelectPhaseInput
        domId="phase-one"
        label="Phase 1"
        name={name}
        checked
        theme={GREEN_THEME}
        setPhase={setPhase}
        phaseNumber={1}
      />
      <SelectPhaseInput
        domId="phase-two"
        label="Phase 2"
        name={name}
        theme={ORANGE_THEME}
        setPhase={setPhase}
        phaseNumber={2}
      />
      <SelectPhaseInput
        domId="phase-three"
        label="Phase 3"
        name={name}
        theme={RED_THEME}
        setPhase={setPhase}
        phaseNumber={3}
      />
    </div>
  );
};

SelectPhase.defaultProps = {
  label: "Choisissez une phase"
};

SelectPhase.propTypes = {
  label: Proptypes.string,
  setPhase: Proptypes.func.isRequired
};

export default SelectPhase;
