import React, { createContext, useReducer } from "react";
import PropTypes from "prop-types";
import HistoryReducer, { initialState } from "../reducer/HistoryReducer";

export const HistoryContext = createContext();

const HistoryContextProvider = ({ children }) => {
  const [history, dispatch] = useReducer(HistoryReducer, initialState);

  return (
    <HistoryContext.Provider value={{ history, dispatch }}>
      {children}
    </HistoryContext.Provider>
  );
};

HistoryContextProvider.propTypes = {
  children: PropTypes.node.isRequired
};

export default HistoryContextProvider;
