import React from "react";
import PropTypes from "prop-types";
import "./SelectPhaseInput.css";

const SelectPhaseInput = ({
  domId,
  label,
  name,
  checked,
  theme,
  setPhase,
  phaseNumber
}) => {
  return (
    <div className="select-phase-input">
      <label className={`label ${theme}`} htmlFor={domId}>
        {label}
        <input
          id={domId}
          type="radio"
          name={name}
          defaultChecked={checked}
          value={phaseNumber}
          onChange={e => setPhase(Number(e.target.value))}
        />
        <span className="checkmark" />
      </label>
    </div>
  );
};

SelectPhaseInput.defaultProps = {
  label: "",
  checked: false
};

SelectPhaseInput.propTypes = {
  domId: PropTypes.string.isRequired,
  label: PropTypes.string,
  theme: PropTypes.string.isRequired,
  name: PropTypes.string.isRequired,
  checked: PropTypes.bool,
  setPhase: PropTypes.func.isRequired,
  phaseNumber: PropTypes.number.isRequired
};

export default SelectPhaseInput;
