import React, { createContext, useState, useEffect } from "react";
import { Link, Route } from "react-router-dom";
import uuidv1 from "uuid/v1";
import PropTypes from "prop-types";
import InfoPage from "../component/Footer/InfoPage/InfoPage";
import { API_URL } from "../config/global";

export const InfoPageContext = createContext();

const InfoPageContextProvider = ({ children }) => {
  const [legal, setLegal] = useState({
    link: null,
    route: null
  });
  const [about, setAbout] = useState({
    link: null,
    route: null
  });

  const fetchData = async () => {
    await fetch(`${API_URL}/informationslegales`)
      .then(res => res.json())
      .then(res => {
        const arr = Object.values(res);
        setLegal({
          ...legal,
          link: arr.map(({ title }) => {
            return (
              <li key={uuidv1()}>
                <Link
                  to={`/${title
                    .toLowerCase()
                    .replace(/ /g, "-")
                    .normalize("NFD")
                    .replace(/[\u0300-\u036f]/g, "")}`}
                >
                  {title}
                </Link>
              </li>
            );
          }),
          route: arr.map(({ title, content }) => {
            return (
              <Route
                key={uuidv1()}
                path={`/${title
                  .toLowerCase()
                  .replace(/ /g, "-")
                  .normalize("NFD")
                  .replace(/[\u0300-\u036f]/g, "")}`}
                render={() => <InfoPage title={title} content={content} />}
              />
            );
          })
        });
      })
      .catch(err => {
        console.log(err);
      });
    await fetch(`${API_URL}/apropos`)
      .then(res => res.json())
      .then(res => {
        const arr = Object.values(res);
        setAbout({
          ...about,
          link: arr.map(({ title }) => {
            return (
              <li key={uuidv1()}>
                <Link
                  to={`/${title
                    .toLowerCase()
                    .replace(/ /g, "-")
                    .normalize("NFD")
                    .replace(/[\u0300-\u036f]/g, "")}`}
                >
                  {title}
                </Link>
              </li>
            );
          }),
          route: arr.map(({ title, content }) => {
            return (
              <Route
                key={uuidv1()}
                path={`/${title
                  .toLowerCase()
                  .replace(/ /g, "-")
                  .normalize("NFD")
                  .replace(/[\u0300-\u036f]/g, "")}`}
                render={() => <InfoPage title={title} content={content} />}
              />
            );
          })
        });
      })
      .catch(err => {
        console.log(err);
      });
  };
  useEffect(() => {
    fetchData();
  }, []);
  return (
    <InfoPageContext.Provider value={{ legal, about }}>
      {children}
    </InfoPageContext.Provider>
  );
};

InfoPageContextProvider.propTypes = {
  children: PropTypes.node.isRequired
};

export default InfoPageContextProvider;
