import { useReducer, useState } from "react";
import {
  openSuggest as openSuggestAction,
  closeSuggest as closeSuggestAction,
  postSuggest as postSuggestAction
} from "../action/actionCreators";
import SuggestModifReducer, {
  initialState
} from "../reducer/SuggestModifReducer";
import { API_URL } from "../config/global";

const useSuggestModif = () => {
  const [state, dispatch] = useReducer(SuggestModifReducer, initialState);
  const [currentProjectSuggest, setCurrentProjectSuggest] = useState(null);

  const openSuggest = cardData => {
    setCurrentProjectSuggest(cardData);
    dispatch(openSuggestAction(cardData.id));
  };

  const closeSuggest = () => {
    dispatch(closeSuggestAction());
  };

  const postSuggest = (userId, formData) => {
    const { id, userSuggest } = currentProjectSuggest;
    const { description, justify, phase } = formData;
    fetch(`${API_URL}/projects/${id}`, {
      method: "PUT",
      headers: {
        "Content-Type": "application/json"
      },
      body: JSON.stringify({
        userSuggest: [
          {
            description,
            justify,
            phase,
            createdAt: new Date(),
            userinfo: {
              id: userId
            }
          },
          ...userSuggest
        ]
      })
    })
      .then(res => res.json())
      .then(res => {
        console.log(res);
        dispatch(postSuggestAction(id, res.userSuggest));
      })
      .catch(err => {
        console.log(err);
      });
  };

  return [
    { state, currentProjectSuggest },
    { openSuggest, closeSuggest, postSuggest }
  ];
};

export default useSuggestModif;
