import React, { useState, useEffect, useContext } from "react";
import GoogleMapReact from "google-map-react";
import Geocode from "react-geocode";
import PlacesAutocomplete, {
  geocodeByAddress,
  getLatLng
} from "react-places-autocomplete";
import Input from "../utils/UserInput/Input/Input";
import SelectPhase from "../utils/SelectPhase/SelectPhase";
import TextArea from "../utils/UserInput/TextArea/TextArea";
import Button from "../utils/Button/Button";
import useApiRequest from "../../hook/useApiRequest";
import {
  FETCHING,
  SUCCESS,
  ERROR,
  FACEBOOK_CONNECTED
} from "../../actionType/actionTypes";
import "./Suggest.css";
import Spinner from "../utils/Spinner/Spinner";
import Modal from "../utils/Modal/Modal";
import FadeIn from "../utils/FadeIn/FadeIn";
import Pin from "../Pin/Pin";
import { AuthContext } from "../../context/AuthContext";
import LoginButtons from "../LoginButtons/LoginButtons";
import { API_URL } from "../../config/global";

const Suggest = () => {
  // Google API key
  const key = "AIzaSyAr2ugrwLtCWxdkM1qLJbgbCPzQqr9oC14";
  // GoogleMapReact settings
  const defaultCenter = { lat: 43.5619687, lng: 1.467865 };
  const [center, setCenter] = useState(defaultCenter);
  const [zoom] = useState(11);

  // Form data
  const { userinfo } = useContext(AuthContext);
  const [title, setTitle] = useState();
  const [address, setAddress] = useState("");
  const [description, setDescription] = useState();
  const [justify, setJustify] = useState();
  const [lat, setLat] = useState();
  const [lng, setLng] = useState();
  const [phase, setPhase] = useState(1);

  // Login
  const [showLogin, setShowLogin] = useState(false);

  // Post data
  const endpoint = `${API_URL}/projects`;
  const [{ status, response }, { makeRequest }] = useApiRequest(endpoint, {
    verb: "POST",
    params: {
      // Check if useinfo false, show login
      userinfo: { id: userinfo.id },
      title,
      address,
      description,
      justify,
      lat,
      lng,
      phase
    }
  });
  useEffect(() => {
    if (userinfo.status === FACEBOOK_CONNECTED) setShowLogin(false);
  }, [userinfo.status]);
  const handleSubmit = e => {
    e.preventDefault();
    if (userinfo.status === FACEBOOK_CONNECTED) {
      makeRequest();
      // TODO: reset on success
      setTitle("");
      setAddress("");
      setDescription("");
      setJustify("");
      setLat(0);
      setLng(0);
      setPhase(1);
    } else setShowLogin(true);
  };
  // For CSSTransition > FadeIn and Modal
  const [modalSuccess, setModalSuccess] = useState(false);
  const [modalError, setModalError] = useState(false);
  useEffect(() => {
    if (status === SUCCESS) {
      setModalSuccess(true);
      setTimeout(() => {
        setModalSuccess(false);
      }, 4000);
    } else if (status === ERROR) {
      setModalError(true);
      setTimeout(() => {
        setModalError(false);
      }, 4000);
    }
  }, [status]);
  // Geocode init
  Geocode.setApiKey(key);
  Geocode.setLanguage("fr");
  Geocode.setRegion("fr");
  Geocode.enableDebug();

  // Add pin from click
  const [newPin, setNewPin] = useState(null);
  const addPinFromClick = coord => {
    setLat(coord.lat);
    setLng(coord.lng);
    setCenter({ lat: coord.lat, lng: coord.lng });
    setNewPin({
      lat: coord.lat,
      lng: coord.lng,
      theme: "green",
      showCard: false
    });
    Geocode.fromLatLng(coord.lat, coord.lng).then(
      res => {
        setAddress(res.results[0].formatted_address);
      },
      error => {
        console.log(`error ${error}`);
      }
    );
  };
  // Add pin from address
  const addPinFromAddress = coord => {
    setLat(coord.lat);
    setLng(coord.lng);
    setNewPin({
      lat: coord.lat,
      lng: coord.lng,
      theme: "green",
      showCard: false
    });
  };
  const NewPin = newPin && address && (
    <Pin
      id={-1}
      lat={newPin.lat}
      lng={newPin.lng}
      showCard={newPin.showCard}
      cardData={{ phase }}
    />
  );
  // Placeautocomplete
  const handleChange = addr => {
    setAddress(addr);
  };
  const handleSelect = addr => {
    setAddress(addr);
    geocodeByAddress(addr)
      .then(results => getLatLng(results[0]))
      .then(latLng => {
        addPinFromAddress({ addr, lat: latLng.lat, lng: latLng.lng });
        setCenter({ lat: latLng.lat, lng: latLng.lng });
      })
      .catch(error => console.error("Error", error));
  };
  return (
    <div className="suggest">
      <div className="left">
        <div className="map-wrapper">
          <GoogleMapReact
            bootstrapURLKeys={key}
            defaultCenter={defaultCenter}
            defaultZoom={zoom}
            onClick={addPinFromClick}
            center={center}
          >
            {NewPin}
          </GoogleMapReact>
        </div>
      </div>
      <div className="right">
        {status === FETCHING && (
          <div className="fetching">
            <Spinner />
          </div>
        )}
        {/* {showLogin && <LoginButtons />} */}
        <LoginButtons />
        <FadeIn inProp={modalSuccess}>
          <div className="modal-container">
            <Modal status="success" closeModal={() => setModalSuccess(false)}>
              Merci de votre contribution !
            </Modal>
          </div>
        </FadeIn>
        <FadeIn inProp={modalError}>
          <div className="modal-container">
            <Modal status="error" closeModal={() => setModalError(false)}>
              Une erreur est survenue {JSON.stringify(response)}
            </Modal>
          </div>
        </FadeIn>
        <form className="suggest-form" onSubmit={handleSubmit}>
          <Input
            domId="suggest-form-title"
            label="Titre"
            value={title}
            setValue={setTitle}
            required
          />
          <PlacesAutocomplete
            value={address}
            onChange={handleChange}
            onSelect={handleSelect}
          >
            {({
              getInputProps,
              suggestions,
              getSuggestionItemProps,
              loading
            }) => {
              const inputProps = getInputProps({
                className: "location-search-input"
              });
              return (
                <div className="input-address">
                  <Input
                    required
                    domId="suggest-form-address"
                    label="Adresse"
                    setValue={setAddress}
                    type={inputProps.type}
                    autoComplete={inputProps.autoComplete}
                    role={inputProps.role}
                    aria-autocomplete={inputProps["aria-autocomplete"]}
                    aria-expanded={inputProps["aria-expanded"]}
                    placeholder={inputProps.placeholder}
                    className={inputProps.className}
                    value={inputProps.value}
                    disabled={inputProps.disable}
                    onKeyDown={inputProps.onKeyDown}
                    onBlur={() => {
                      inputProps.onBlur();
                      if (suggestions[0] && suggestions[0].description)
                        handleSelect(suggestions[0].description);
                    }}
                    onChange={inputProps.onChange}
                  />
                  <div
                    className={`dropdown ${
                      suggestions.length > 0 ? "show" : "hide"
                    }`}
                  >
                    <div className="autocomplete-dropdown-container">
                      {loading && <div>Loading...</div>}
                      {suggestions.map(suggestion => {
                        const className = suggestion.active
                          ? "suggestion-item--active"
                          : "suggestion-item";
                        // inline style for demonstration purpose
                        const style = suggestion.active
                          ? { backgroundColor: "#fafafa", cursor: "pointer" }
                          : { backgroundColor: "#ffffff", cursor: "pointer" };
                        const suggestionItemProps = getSuggestionItemProps(
                          suggestion,
                          {
                            className,
                            style
                          }
                        );
                        return (
                          <div
                            key={suggestionItemProps.key}
                            id={suggestionItemProps.id}
                            role="option"
                            aria-selected={false}
                            tabIndex={0}
                            onMouseEnter={suggestionItemProps.onMouseEnter}
                            onMouseLeave={suggestionItemProps.onMouseLeave}
                            onMouseDown={suggestionItemProps.onMouseDown}
                            onMouseUp={suggestionItemProps.onMouseUp}
                            onTouchStart={suggestionItemProps.onTouchStart}
                            onTouchEnd={suggestionItemProps.onTouchEnd}
                            onClick={suggestionItemProps.onClick}
                            onKeyDown={suggestionItemProps.onClick}
                            className={suggestionItemProps.className}
                            style={suggestionItemProps.style}
                          >
                            <span>{suggestion.description}</span>
                          </div>
                        );
                      })}
                    </div>
                  </div>
                </div>
              );
            }}
          </PlacesAutocomplete>
          <SelectPhase setPhase={setPhase} />
          <TextArea
            domId="description-suggestion"
            label="Description"
            value={description}
            setValue={setDescription}
            required
          />
          <TextArea
            domId="justify-suggestion"
            label="Justifiez brievement votre choix de phase"
            value={justify}
            setValue={setJustify}
            required
          />
          <Button type="submit">Confirmer</Button>
        </form>
      </div>
    </div>
  );
};

export default Suggest;
