import {
  OPEN_SUGGEST,
  CLOSE_SUGGEST,
  POST_SUGGEST
} from "../actionType/actionTypes";

export const initialState = {
  projectId: null,
  show: false
};

const SuggestModifReducer = (state = initialState, { type, payload } = {}) => {
  switch (type) {
    case OPEN_SUGGEST:
      return { ...state, projectId: payload.projectId, show: true };
    case CLOSE_SUGGEST:
      return { ...initialState };
    case POST_SUGGEST:
      return Object.values(state).map(pin => {
        if (pin.id === payload.projectId) {
          return { ...pin, userSuggest: payload.userSuggest };
        }
        return { ...pin };
      });
    default:
      return { ...initialState };
  }
};

export default SuggestModifReducer;
