import React, { useReducer } from "react";
import { Link } from "react-router-dom";
import logo from "../../assets/images/cc-logo.png";
import MenuReducer from "../../reducer/MenuReducer";
import { HIDE_MENU, TOGGLE_MENU } from "../../actionType/actionTypes";
import "./Nav.css";

const Nav = () => {
  const [menu, dispatch] = useReducer(MenuReducer, false);

  return (
    <nav className="nav">
      <div className="container">
        <Link className="link" to="/">
          <img className="logo" src={logo} alt="logo" />
        </Link>
        <div
          onClick={() => dispatch({ type: TOGGLE_MENU })}
          onKeyDown={() => dispatch({ type: TOGGLE_MENU })}
          className="burger"
          role="button"
          tabIndex={0}
        >
          <span className="line" />
          <span className="line" />
          <span className="line" />
        </div>
      </div>

      <div className={`nav-menu ${menu ? "show" : "hide"}`}>
        <ul className="nav-ul">
          <li className="nav-li">
            <Link
              onClick={() => dispatch({ type: HIDE_MENU })}
              onKeyDown={() => dispatch({ type: HIDE_MENU })}
              className="link"
              to="/"
            >
              Accueil
            </Link>
          </li>
          <li className="nav-li">
            <Link
              onClick={() => dispatch({ type: HIDE_MENU })}
              onKeyDown={() => dispatch({ type: HIDE_MENU })}
              className="link"
              to="/about-us"
            >
              Qui sommes-nous ?
            </Link>
          </li>
          <li className="nav-li">
            <Link
              onClick={() => dispatch({ type: HIDE_MENU })}
              onKeyDown={() => dispatch({ type: HIDE_MENU })}
              className="link"
              to="/suggest"
            >
              Suggrer un lieu
            </Link>
          </li>
          <li className="nav-li">
            <Link
              onClick={() => dispatch({ type: HIDE_MENU })}
              onKeyDown={() => dispatch({ type: HIDE_MENU })}
              className="link"
              to="/contact"
            >
              Contact
            </Link>
          </li>
        </ul>
      </div>
    </nav>
  );
};

export default Nav;
