import { useReducer, useEffect, useState } from "react";
import {
  facebookConnected,
  facebookNotAuthorized,
  facebookUnknown,
  googleConnected,
  googleNotAuthorized,
  googleUnknown
} from "../action/actionCreators";
import AuthReducer, { initialState } from "../reducer/AuthReducer";
import { API_URL, GOOGLE_API_KEY } from "../config/global";

// Allows to login, logout, check status, from facebook
// Creates a new user in the DB if never visited the website

const useAuth = () => {
  const [userinfo, dispatch] = useReducer(AuthReducer, initialState);

  // === misc ===
  const createNewUser = async (name, socialId) => {
    return fetch(`${API_URL}/userinfos`, {
      method: "POST",
      headers: {
        "Content-Type": "application/json"
      },
      body: JSON.stringify({
        username: name,
        userId: socialId,
        last_connection: new Date()
      })
    })
      .then(res => res.json())
      .then(res => {
        console.log(res);
        return res;
      })
      .catch(err => {
        console.log(err);
        return err;
      });
  };

  const findUserByUserId = (list, id) => {
    return list.filter(item => item.userId === id);
  };

  const getAccountList = async userId => {
    return fetch(`${API_URL}/userinfos`)
      .then(res => res.json())
      .then(res => {
        const user = findUserByUserId(res, userId);
        if (!user || user.length < 0) return false;
        return user;
      })
      .catch(err => {
        console.log(err);
        return false;
      });
  };

  // === Facebook ===
  const createAccountFromFacebook = async () => {
    window.FB.api("/me", ({ name, id }) => {
      createNewUser(name, id).then(res => {
        dispatch(facebookConnected(res.username, res.userId, res.id));
      });
      console.log(
        `Welcome ${name}, we are creating your Checkcheck account...`
      );
    });
  };

  const handleFacebookResponse = response => {
    const { authResponse, status } = response;
    switch (status) {
      case "connected":
        getAccountList(authResponse.userID).then(user => {
          if (!user || user.length <= 0) {
            createAccountFromFacebook();
          } else {
            dispatch(
              facebookConnected(user[0].username, user[0].userId, user[0].id)
            );
          }
        });
        break;
      case "not_authorized":
        console.log("Unable to automatically connect");
        dispatch(facebookNotAuthorized());
        break;
      case "unknown":
        console.log("Connection status: unknown");
        dispatch(facebookUnknown());
        break;
      default:
        console.log("Connection status: unknown");
        dispatch(facebookUnknown());
    }
  };

  const initFacebookLogin = () => {
    window.fbAsyncInit = () => {
      window.FB.Event.subscribe("auth.statusChange", res => {
        handleFacebookResponse(res);
      });
      window.FB.init({
        appId: "462870114620083",
        cookie: true, // enable cookies to allow the server to access the session
        status: true,
        xfbml: true, // parse social plugins on this page
        version: "v5.0" // use version 5.0
      });
    };
    console.log("Loading facebook api");
    // Load the SDK asynchronously
    ((d, s, id) => {
      const fjs = d.getElementsByTagName(s)[0];
      if (d.getElementById(id)) return;
      const js = d.createElement(s);
      js.id = id;
      js.src = "//connect.facebook.net/en_US/sdk.js";
      fjs.parentNode.insertBefore(js, fjs);
    })(document, "script", "facebook-jssdk");
  };

  const facebookLogin = () => {
    window.FB.login(() => {
      window.FB.getLoginStatus(res => {
        handleFacebookResponse(res);
      });
    });
  };

  // === Google ===
  const [auth2, setAuth2] = useState();

  const createAccountFromGoogle = (username, socialId) => {
    createNewUser(username, socialId).then(res => {
      dispatch(googleConnected(res.username, res.userId, res.id));
    });
  };

  const handleGoogleResponse = response => {
    console.log(response);
    if (!response || !response.w3) {
      console.log(response);
      dispatch(googleNotAuthorized());
    } else {
      const { ig: username, Eea: userId } = response.w3;
      getAccountList(userId, createAccountFromGoogle).then(user => {
        if (!user || user.length <= 0) {
          createAccountFromGoogle(username, userId);
        } else {
          dispatch(
            googleConnected(user[0].username, user[0].userId, user[0].id)
          );
        }
      });
    }
  };

  const initGoogleLogin = () => {
    window.gapi.load("auth2", () => {
      // Retrieve the singleton for the GoogleAuth library and set up the client.
      setAuth2(
        window.gapi.auth2.init({
          client_id: GOOGLE_API_KEY,
          cookiepolicy: "single_host_origin"
          // Request scopes in addition to 'profile' and 'email'
          // scope: 'additional_scope'
        })
      );
    });
  };

  const googleLogin = () => {
    auth2.signIn().then(
      res => {
        handleGoogleResponse(res);
      },
      res => {
        console.log(res);
      }
    );
  };

  // === Login fx ===
  const login = social => {
    switch (social) {
      case "facebook":
        facebookLogin();
        break;
      case "google":
        googleLogin();
        break;
      case "instagram":
        break;
      default:
        facebookLogin();
        break;
    }
  };

  // === Logout fx ===
  const logout = () => {
    window.FB.logout();
    dispatch(facebookNotAuthorized());
  };

  useEffect(() => {
    initFacebookLogin();
    initGoogleLogin();
  }, []);

  return [userinfo, { login, logout }];
};

export default useAuth;
