import {
  FACEBOOK_CONNECTED,
  FACEBOOK_NOT_AUTHORIZED,
  FACEBOOK_UNKNOWN,
  GOOGLE_CONNECTED,
GOOGLE_NOT_AUTHORIZED,
GOOGLE_UNKNOWN
} from "../actionType/actionTypes";

export const initialState = {
  id: null,
  username: null,
  userId: null,
  status: FACEBOOK_UNKNOWN
};

const AuthReducer = (state = initialState, { type, payload } = {}) => {
  switch (type) {
    case FACEBOOK_CONNECTED:
      return {
        ...state,
        status: FACEBOOK_CONNECTED,
        username: payload.username,
        userId: payload.userId,
        id: payload.id
      };
    case FACEBOOK_NOT_AUTHORIZED:
      return { ...state, status: FACEBOOK_NOT_AUTHORIZED };
    case FACEBOOK_UNKNOWN:
      return { ...state, status: FACEBOOK_UNKNOWN };
    default:
      return { ...state, status: FACEBOOK_UNKNOWN };
  }
};

export default AuthReducer;
